import React from 'react';
import { Pie } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  ArcElement,
  Tooltip,
  Legend,
  CategoryScale,
  LinearScale,
  Title,
} from 'chart.js';

//  Chart.js   .
ChartJS.register(
  ArcElement,
  Tooltip,
  Legend,
  CategoryScale,
  LinearScale,
  Title
);

function Piechart({ matchScore }) {
  // matchScore   Pie Chart   .
  const data = {
    labels: [' '],
    datasets: [
      {
        label: 'Probability',
        data: [matchScore, 100 - matchScore], // matchScore    
        backgroundColor: [
          'rgba(102, 0, 204, 0.2)', // 
          'rgba(220, 220, 220, 0.8)' //   

        ],
        borderColor: [
          'rgba(102, 0, 204, 1)',
          'rgba(169, 169, 169, 1)',
        ],
        borderWidth: 1,
      },
    ],
  };

 

  return <Pie data={data} />;
}




export default Piechart;