
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import API_BASE_URL from '../Config';
import { Link } from 'react-router-dom';

function Board2() {
  const [jobs, setJobs] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const pageSize = 20;

  const [maxPageNumberLimit, setMaxPageNumberLimit] = useState(10);
  const [minPageNumberLimit, setMinPageNumberLimit] = useState(0);

  //   ID
  const [selectedCategory, setSelectedCategory] = useState(518); //  '' 

  const categoryNames = {
    "": "",
    "518": "",
    "507": "",
    "523": "",
    "511": "",
    "530": "",
    "510": "",
    "524": "",
    "513": "",
    "517": "HR",
    "959": " ",
    "508": "",
    "522": "",
    "515": "",
    "532": "",
    "10057": "",
    "521": "",
    "509": "",
    "514": ""
    //    
  };


  useEffect(() => {
    const categoryURL = `${API_BASE_URL}/jobs/${selectedCategory}`
    axios.get(categoryURL, {
      params: {
        page: page - 1,
        size: pageSize
      }
    })
      .then((response) => {
        const jobData = response.data.content.map(job => ({
          title: job.title,          
          id: job.id,
          titleImg: job.titleImg,
        }));
        setJobs(jobData);
        setTotalPages(response.data.totalPages);
      })
      .catch((error) => {
        console.error('    : ', error);
      });
  }, [page, selectedCategory]);

  const handleNextTen = () => {
    setMaxPageNumberLimit(maxPageNumberLimit + 10);
    setMinPageNumberLimit(minPageNumberLimit + 10);
  };

  const handlePrevTen = () => {
    setMaxPageNumberLimit(maxPageNumberLimit - 10);
    setMinPageNumberLimit(minPageNumberLimit - 10);
  };

  const renderPageNumbers = () => {
    const pageNumbers = [];
    for (let i = minPageNumberLimit + 1; i <= Math.min(maxPageNumberLimit, totalPages); i++) {
      pageNumbers.push(
        <button
          key={i}
          onClick={() => setPage(i)}
          style={{
            marginTop: '50px',
            marginRight: '30px',
            color: 'violet',
            backgroundColor: page === i ? '#D8BFD8' : '#E6E6FA',
            border: '2px solid #DDA0DD',
            borderRadius: '5px',
            padding: '5px 5px',
            fontSize: '1rem',
            cursor: 'pointer',
            transition: 'background-color 0.3s'
          }}
        >
          {i}
        </button>
      );
    }

    return (
      <div style={{ display: 'flex', justifyContent: 'center', marginTop: '20px' }}>
        <button
          onClick={handlePrevBtn}
          disabled={page === 1}
          style={{
            marginTop: '50px',
            marginRight: '30px',
            color: 'black',
            borderRadius: '5px',
            padding: '5px 5px',
            fontSize: '1rem',
            cursor: 'pointer',
            transition: 'background-color 0.3s'
          }}
        >
          
        </button>
        {minPageNumberLimit >= 1 && (
          <button onClick={handlePrevTen} style={{ /*  */ }}>

          </button>
        )}
        {pageNumbers}
        {totalPages > maxPageNumberLimit && maxPageNumberLimit < totalPages && (
          <button onClick={handleNextTen} style={{ /*  */ }}>

          </button>
        )}
        <button
          onClick={handleNextBtn}
          disabled={page === totalPages}
          style={{
            marginTop: '50px',
            marginRight: '30px',
            color: 'black',
            borderRadius: '5px',
            padding: '5px 5px',
            fontSize: '1rem',
            cursor: 'pointer',
            transition: 'background-color 0.3s'
          }}
        >
          
        </button>
      </div>
    );
  }

  const handlePrevBtn = () => {
    setPage(prevPage => prevPage - 1);
    if ((page - 1) % 10 === 0) {
      handlePrevTen();
    }
  };

  const handleNextBtn = () => {
    setPage(nextPage => nextPage + 1);
    if (page + 1 > maxPageNumberLimit) {
      handleNextTen();
    }
  };

  return (
    <div className="ml-24 mb-20 mr-24 mt-6"
    style={{
      paddingTop: '190px'
    }}>
      {/*    */}
      <div>
        <label
          htmlFor="categorySelect"
          style={{
            paddingTop: '3px',
            marginRight: '15.5px',
            fontSize: '28px',
            lineHeight: '25px',
            fontWeight: '700',
            color: '#333'
          }}
        >
          {categoryNames[selectedCategory]}
        </label>

        <select
          id="categorySelect"
          value={selectedCategory}
          onChange={(e) => setSelectedCategory(e.target.value)}
          style={{
            position: 'relative',
            border: '1px solid #e1e2e3',
            borderRadius: '9999px',
            backgroundColor: '#fff',
            width: '21px',
            height: '26px',
            WebkitTransition: '.3s',
            transition: '.3s',
            flexShrink: '0'
           

          }}
        >
          <option value=""></option>
          <option value="518"></option>
          <option value="507"></option>
          <option value="523"></option>
          <option value="511"></option>
          <option value="530"></option>
          <option value="510"></option>
          <option value="524"></option>
          <option value="513"></option>
          <option value="517">HR</option>
          <option value="959"> </option>
          <option value="508"></option>
          <option value="522"></option>
          <option value="515"></option>
          <option value="532"></option>
          <option value="10057"></option>
          <option value="521"></option>
          <option value="509"></option>
          <option value="514"></option>

        </select>
      </div>
      <div className="grid grid-cols-4 mt-5 pt-6 gap-5">
        {jobs.map((job, index) => (
          <div key={index} className="max-w-sm rounded overflow-hidden shadow-lg">
            <Link to={`/Recruitment/detail/${job.id}`}>
              <img className="w-full" src={job.titleImg} alt={job.title} />
              <div className="px-6 py-4">
                <p className="font-bold text-1/2 mb-2"
                  style={{
                    textAlign: 'center'
                  }}>{job.title}</p>
              </div>
            </ Link> 
          </div>
        ))}
      </div>
      <div className="pagination pb-6" >
        {renderPageNumbers()}
      </div>
    </div>
  );
}

export default Board2;
